.button.white,
header .top a,
nav a {
  font-style: normal
}

#mentions-legales h2 {
  margin-top: 0
}

#mentions-legales .panel {
  font-size: 1.1em
}

#product .info {
  float: right;
  width: 25%;
  margin: 0 0 20px 20px;
  padding: 5px 10px;
  background: #f5f5f5
}

#product .info .thumbnail {
  margin-bottom: 0
}

#product .info .little {
  width: 50px;
  float: left
}

#cart input[type=number] {
  width: 40px
}

body {
  font-size: 16px
}

a:hover {
  text-decoration: none;
  color: #d2232a
}

header .phone-resp {
  display: none
}

.bgblack {
  background-color: rgba(0, 0, 0, .5);
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%
}

header.slider {
  position: relative;
  background-image: url(../img/slider1.jpg);
  height: 750px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat
}

header .middle {
  width: 100%;
  margin: auto;
  position: absolute;
  left: 50%;
  top: 58%;
  transform: translate(-50%, -50%)
}

header .menu,
header .top,
nav a,
nav li {
  position: relative
}

header .middle h1 {
  color: #fff;
  font-size: 55px;
  font-weight: 500
}

header .middle h2 {
  color: #fff;
  font-size: 35px;
  font-weight: 400
}

header .middle .pre-reservation h2 {
  color: #333;
  font-size: 45px;
  font-weight: 300
}

header .middle h3 {
  color: #fff;
  font-size: 25px;
  font-weight: 300
}

header .top {
  background-color: #238bfa;
  padding: 10px 0
}

header .top h3 {
  margin: 0
}

header .top a {
  font-weight: 500;
  color: #fff;
  line-height: 1.4;
  letter-spacing: 0;
  font-size: 14px
}

header .top .button {
  padding: 5px 15px;
  border-radius: 4px;
  background-color: #fff;
  border: none;
  color: #333
}

header .top .button:hover {
  background-color: #09739f;
  color: #fff
}

header .social a {
  color: #fff;
  padding-right: 10px
}

nav a.active,
nav a:hover {
  color: #238bfa;
  text-decoration: none
}

header .pre-reservation {
  padding: 15px;
  background-color: rgba(255, 255, 255, .5);
  border-radius: 4px
}

.navbar-collapse.collapse,
nav ul {
  padding: 0
}

nav {
  padding-top: 5px
}

nav li {
  display: inline-block
}

nav a {
  font-weight: 500;
  color: #333;
  text-decoration: none;
  border-color: transparent;
  font-size: 16px;
  line-height: 28px;
  display: block;
  text-align: left;
  transition: .3s;
  z-index: 1;
  padding: 10px
}

nav a.active {
  margin-left: 6px
}

nav ul {
  margin: 0
}

header .menu img {
  padding-top: 15px;
  width: 150px;
  margin: auto
}

header .menu {
  background-color: #fff;
  padding: 15px 0;
  width: 100%;
  min-height: 80px
}

header .fixmenu {
  background: #fff;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 999
}

.inner-predevis,
.redColor ul {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)
}

section p {
  font-size: 18px;
  color: #747474
}

section.one {
  padding: 70px 0
}

section.three,
section.two {
  background-size: cover;
  background-position: center;
  min-height: 400px;
  padding-top: 60px;
  padding-bottom: 100px
}

section.one h2 {
  color: #00b3fe;
  font-size: 45px;
  font-weight: 500;
  margin-bottom: 20px
}

section.two {
  background-image: url(../img/slider3.jpg)
}

section.two h2 {
  color: #fff;
  font-size: 25px;
  font-weight: 700;
  text-align: center;
  margin-bottom: 20px
}

section.two p {
  color: #fff;
  font-weight: 500;
  font-size: 20px
}

section.three h3 {
  color: #fff;
  font-size: 25px;
  font-weight: 500;
  text-align: center;
  margin-bottom: 20px
}

section.three.home .fa {
  font-size: 45px;
  color: #fff
}

section.three a,
section.three p {
  color: #fff
}

section.three {
  background-image: url(../img/slider2.jpg);
  position: relative
}

section.three.noPadding {
  padding: 0
}

section.five {
  padding: 70px 0
}

section.one .button {
  color: #fff;
  background-color: #09739f;
  border: 1px solid #09739f;
  box-shadow: 0 0 20px 3px rgba(46, 161, 219, .3);
  padding: 15px 25px;
  transition: .3s;
  border-radius: 30px
}

.button,
section.one .button:hover {
  color: #fff;
  background-color: #238bfa;
  border: 1px solid #238bfa
}

section.one .button:hover {
  box-shadow: none;
  text-decoration: none
}

.button {
  box-shadow: 0 0 20px 3px rgba(46, 161, 219, .3);
  padding: 7px 15px;
  transition: .3s;
  border-radius: 4px;
  position: relative
}

.button-phone,
.button:hover {
  background-color: #09739f;
  border: 1px solid #09739f;
  color: #fff
}

.button:hover {
  box-shadow: none;
  text-decoration: none
}

.button-phone {
  font-size: 35px;
  box-shadow: 0 0 20px 3px rgba(46, 161, 219, .3);
  padding: 15px 35px;
  transition: .3s;
  border-radius: 4px
}

.button-phone:hover {
  color: #fff;
  background-color: #238bfa;
  box-shadow: none;
  text-decoration: none;
  border: 1px solid #238bfa
}

.button.white {
  color: #333;
  background-color: #fff;
  border: 1px solid #fff;
  box-shadow: 0 0 20px 3px rgba(46, 161, 219, .3);
  padding: 7px 15px;
  transition: .3s;
  border-radius: 4px
}

.button.white:hover {
  color: #fff;
  background-color: #09739f;
  box-shadow: none;
  text-decoration: none;
  border: 1px solid #09739f
}

.round-content {
  background-color: rgba(1, 237, 201, .7);
  border-radius: 100%;
  width: 250px;
  margin: auto;
  height: 250px;
  overflow: hidden;
  text-align: center;
  padding: 50px 15px 0
}

.round-content .fa,
.round-icon .fa {
  color: #fff;
  font-size: 45px
}

.opinion .fa-quote-left {
  color: #09739f;
  font-size: 30px
}

.round-icon {
  background-color: #00b3fe;
  width: 100px;
  height: 100px;
  margin: auto;
  text-align: center;
  line-height: 120px;
  border-radius: 50%
}

.icon-inner {
  background-color: #fff;
  width: 80px;
  height: 80px;
  margin: auto;
  color: #d2232a;
  font-size: 35px;
  line-height: 80px;
  border-radius: 100%
}

.bandeau-video {
  position: relative;
  width: 100%;
  height: 550px;
  overflow: hidden;
  text-align: center;
  color: #fff
}

.inner-predevis {
  width: 100%;
  margin: auto;
  position: absolute;
  background-color: rgba(0, 0, 0, .5);
  padding: 30px 0
}

.noPadding,
.redColor {
  padding: 0
}

.bandeau-video video {
  width: 100%;
  margin-bottom: -5px;
  overflow: hidden
}

.redColor {
  background-color: #d2232a;
  height: 450px;
  position: relative;
  color: #fff
}

.redColor ul {
  width: 100%;
  position: absolute
}

.redColor li {
  font-size: 18px;
  list-style: none
}

.form-control {
  height: 42px;
  border: 1px solid #ccc;
  border-radius: 4px
}

.has-feedback .form-control-feedback {
  top: 30px
}

.three .has-feedback .form-control-feedback {
  top: 0
}

footer {
  background-color: #111;
  text-align: left;
  color: #fff
}

#contact,
.panneauxCSS {
  text-align: center
}

footer a {
  color: #fff;
  text-decoration: none;
  font-size: 12px
}

footer li {
  list-style: none
}

footer ul {
  padding-left: 0
}

footer h3 {
  color: #d2232a;
  font-weight: 300
}

.lien_pprofonde {
  color: #999
}

.panneauxCSStop {
  border: 5px solid #fcfcfc;
  border-radius: 18px;
  box-shadow: 5px 5px 10px #CCCECF;
  float: right
}

.panneauxCSS {
  font-family: arial;
  font-weight: 700;
  font-size: 30px;
  border: 9px solid #b91425;
  border-radius: 13px;
  padding: 10px 20px;
  background-color: #999;
  background: linear-gradient(135deg, #fcfcfc 63%, #eaeaea 100%)
}

.img-inner img {
  width: 200px;
  margin: auto
}

.inner-video video {
  width: 100%;
  margin: auto
}

.inner-list li {
  list-style: none;
  color: #fff
}

.gal .thumb-inner {
  width: 200px;
  margin: auto
}

.gal {
  -webkit-column-count: 3;
  -moz-column-count: 3;
  column-count: 3
}

.gal img {
  width: 100%;
  padding: 7px 0
}

@media (max-width:500px) {
  .gal {
    -webkit-column-count: 1;
    -moz-column-count: 1;
    column-count: 1
  }
}

#lightbox .modal-content {
  display: inline-block;
  text-align: center
}

#lightbox .close {
  opacity: 1;
  color: #fff;
  background-color: #191919;
  padding: 5px 8px;
  border-radius: 30px;
  border: 2px solid #fff;
  position: absolute;
  top: -15px;
  right: -55px;
  z-index: 1032
}

.pseudoForm {
  display: none
}

.has-error.group-rgpd {
  border: 1px solid #a94442;
  border-radius: 4px;
  padding: 6px 12px
}

.group-rgpd.has-feedback .form-control-feedback {
  top: 0
}

@media (max-width:992px) {
  nav a {
    font-size: 12px
  }
}

@media (max-width:767px) {

  .img-responsive,
  .round-content {
    margin-bottom: 1em;
    width: 100%;
  }

  nav a {
    font-size: 18px
  }

  header .middle {
    position: inherit;
    left: inherit;
    top: inherit;
    transform: inherit
  }

  .top {
    display: none
  }

  .navbar-collapse li,
  .phone-resp {
    display: block
  }

  header.slider {
    padding: 0 0 70px;
    height: auto
  }

  header .middle h1 {
    font-size: 40px
  }

  .button-phone {
    font-size: 18px
  }

  .navbar-toggle {
    background-color: #238bfa
  }

  .navbar-collapse.in,
  .navbar-toggle .icon-bar {
    background-color: #fff
  }

  .navbar-collapse.in {
    border-top: 1px solid #09739f;
    position: relative;
    z-index: 99
  }

  nav a.active,
  nav a:hover {
    border-bottom: none
  }

  nav li:not(:first-child):after {
    background-color: transparent
  }

  .img-responsive {
    margin-top: 2em
  }

  .opinion .col-sm-4 {
    margin-bottom: 2em
  }

  footer {
    text-align: center
  }

  .phone-resp {
    padding: 15px 0
  }
}

.w-100 {
  width: 100% !important;
}

.app1 img {
  height: 50px;
}

.pad10 {
  padding: 10px 0px;
}

.bold {
  font-weight: bold;
}

