section.bg-paralax2:before {
  left      : auto;
  right     : 0;
  background: url(../img/dots.png) #FFF6F1 no-repeat center right
}

@media only screen and (min-width:768px) {
  section.bg-paralax2:before {
      width: 60%
  }
}

.carousel4 .item {
  padding: 35px 0
}

.tab {
  margin: 0
}

.nav-pills li a {
  background-color  : #f6f6f6;
  text-transform    : uppercase;
  border            : 0;
  margin            : 0;
  border-radius     : 0;
  border-bottom     : 2px solid #fff !important;
  outline           : 0;
  cursor            : pointer;
  font-weight       : 700;
  text-align        : left;
  padding           : 14px 16px;
  -webkit-transition: .4s;
  transition        : .4s;
  font-size         : 12px;
  font-family       : Montserrat, sans-serif;
  color             : #1f1f1f;
  letter-spacing    : 2px;
  display           : block;
  position          : relative
}

.nav-pills li a:before {
  position         : absolute;
  display          : inline-block;
  top              : 50%;
  right            : 4%;
  -webkit-transform: translate(0, -50%);
  -ms-transform    : translate(0, -50%);
  transform        : translate(0, -50%);
  z-index          : 0;
  font             : 14px/1 FontAwesome;
  color            : #f6f6f6;
  content          : "\f054";
  text-align       : center
}

.nav-pills li a:hover {
  background   : #f2ccc8;
  color        : #fff;
  border-bottom: 2px solid #f2ccc8 !important
}

.nav-pills>li.active>a,
.nav-pills>li.active>a:hover {
  background   : #f2ccc8 !important;
  outline      : 0;
  color        : #fff;
  border-bottom: 1px solid #f2ccc8 !important
}

.nav-stacked>li+li {
  margin-top: 0 !important
}

.tabcontent h3 {
  font-family   : Montserrat, sans-serif;
  font-size     : 16px;
  font-weight   : 700;
  text-transform: uppercase;
  color         : #1f1f1f
}

.tarif {
  padding           : 50px 30px;
  border-radius     : 7px;
  overflow          : hidden;
  position          : relative;
  margin-bottom     : 20px;
  text-align        : center;
  -webkit-box-shadow: 0 0 17px 0 rgba(72, 73, 121, .05);
  box-shadow        : 0 0 17px 0 rgba(72, 73, 121, .05)
}

.tarif:before {
  background       : #FFF6F1;
  content          : "";
  height           : 280px;
  left             : -25%;
  position         : absolute;
  top              : -10%;
  -webkit-transform: rotate(-10deg);
  -ms-transform    : rotate(-10deg);
  transform        : rotate(-10deg);
  width            : 150%;
  z-index          : -1
}

.tarif p {
  text-align: center !important;
  font-size : 14px
}

.tarif h3 {
  font-weight: 800;
  color      : #1f1f1f;
  font-size  : 45px
}

.tarif h4 {
  font-size     : 15px;
  letter-spacing: 1px;
  text-transform: uppercase;
  font-weight   : 600;
  color         : #f2ccc8;
  margin        : 20px 0
}

.bgContact {
  background: url(../img/dots.png) #FFF6F1 no-repeat center right;
  position  : relative;
  overflow  : hidden
}

.bgContact .title5 {
  font-size     : 100px;
  text-transform: uppercase;
  color         : #fff;
  position      : absolute;
  top           : -17%;
  left          : 10%;
  font-weight   : 800;
  font-family   : Montserrat, sans-serif
}

@media only screen and (min-width:768px) {
  .bgContact .title5 {
      font-size: 170px
  }
}

.precontact .form-control {
  background: #fff !important
}

.marque img {
  -webkit-box-shadow: 0 0 5px 0 rgba(72, 73, 121, .15);
  box-shadow        : 0 0 5px 0 rgba(72, 73, 121, .15);
  border-radius     : 3px
}

#map {
  height: 350px
}

.table1,
.table2 {
  border     : 1px solid #fff6f1;
  font-family: 'Open Sans', sans-serif;
  width      : 100%
}

.table1 tr:nth-child(odd),
.table2 tr:nth-child(odd) {
  background: #FFF6F1
}

.table1 td,
.table2 td {
  padding: 10px 15px
}

.table1 .button1,
.table2 .button1 {
  margin: 10px 0
}

.table1 .text,
.table2 .text {
  padding: 30px 45px
}

.table1 p,
.table2 p {
  margin: 0
}

.table2 tr {
  display            : -webkit-flex;
  display            : -ms-flexbox;
  display            : -ms-flex;
  display            : flex;
  -webkit-align-items: center;
  -ms-align-items    : center;
  align-items        : center;
  -webkit-flex-wrap  : wrap;
  -ms-flex-wrap      : wrap;
  flex-wrap          : wrap
}

@media only screen and (min-width:992px) {
  .table2 tr {
      -webkit-flex-wrap: no-wrap;
      -ms-flex-wrap    : no-wrap;
      flex-wrap        : no-wrap
  }
}

.table2 td {
  text-align       : left;
  width            : 100%;
  -webkit-flex-grow: 1;
  -ms-flex-grow    : 1;
  flex-grow        : 1
}

@media only screen and (min-width:992px) {
  .table2 td {
      width: auto
  }
}

.table2 td:nth-child(1) {
  width: 100%
}

@media only screen and (min-width:992px) {
  .table2 td:nth-child(1) {
      width: 20%
  }
}

.table2 .text {
  width: 55%
}

.time {
  background   : #bc9632;
  color        : #fff;
  display      : inline-block;
  padding      : 2px 20px;
  border-radius: 20px;
  font-weight  : 700;
  margin       : 7px 0
}

section.one .image3 {
  position  : relative;
  background: #f2ccc8
}

section.one .image3 img {
  opacity: .7;
  filter : alpha(opacity=70)
}

section.one .image3 h2 {
  position         : absolute;
  top              : 50%;
  left             : 50%;
  width            : 100%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform    : translate(-50%, -50%);
  transform        : translate(-50%, -50%);
  font-family      : Parisienne, cursive !important;
  text-transform   : none;
  font-size        : 40px;
  font-weight      : 500
}

/*  */
.fix-btn a {
  position          : fixed;
  -webkit-box-shadow: 0 14px 36px rgba(41, 48, 67, .1);
  box-shadow        : 0 14px 36px rgba(41, 48, 67, .1);
  bottom            : 54px;
  right             : 10px;
  -webkit-transition: .3s;
  transition        : .3s;
  background        : #f2ccc8;
  font-family       : 'Open Sans', sans-serif;
  color             : #333;
  font-weight       : 600;
  border-radius     : 2px;
  padding           : 10px 20px;
  z-index           : 50;
  border            : 1px solid #fff;
  text-align        : center;
}

.menu-top{

  @media only screen and (max-width: 990px) {
    background-color: #fff !important;
    width: 100%;
  }
}

.dropdown-toggle::after {
  display: none !important;
  color: #fff !important;
}

.navbar-active {
  position: fixed !important;
  width: 100%;
  max-width: 100%;
  background-color: #fff !important;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.12);
  border: 0 !important;
  top: 0 !important;
  left: 0 !important;
  transition: 0.2s;
  z-index: 1000 !important;
  padding: 1rem 0;
}



